import { useContext } from "react";
import { BondContext } from "../BondsPolicy";
import { TextField, Divider, Button } from "@mui/material";
import CustomDatePicker from "../../../../../../../../components/DatePicker";

export default function BondsPolicyPremium() {
  const {
    state,
    handleInputChange,
    customInputchange,
    computation,
    isAddOrEditMode,
  } = useContext(BondContext);

  return (
    <div style={{ display: "flex" }}>
      <div
        style={{
          display: "flex",
          columnGap: "10px",
          flex: 1,
          flexDirection: "column",
        }}
      >
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            rowGap: "10px",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
            flex: 1,
          }}
        >
          <legend>Information for notary (Officer)</legend>
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Name"
            name="officerName"
            value={state.officerName}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Tax Certificate No."
            name="officerTaxCertNo"
            value={state.officerTaxCertNo}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Issued Location"
            name="officerIssuedLoc"
            value={state.officerIssuedLoc}
            onChange={handleInputChange}
          />
          <CustomDatePicker
            disabled={isAddOrEditMode}
            label="Date Issued"
            onChange={(e: any) => {
              customInputchange(e, "officerDateIssued");
            }}
            value={new Date(state.officerDateIssued)}
          />
        </fieldset>
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            rowGap: "10px",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
            flex: 1,
          }}
        >
          <legend>Information for notary (Insurance Corp)</legend>
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Capacity as"
            name="insuranceCapacity"
            value={state.insuranceCapacity}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Tax Certificate No"
            name="insuranceOfficerTaxCert"
            value={state.insuranceOfficerTaxCert}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Issued Location"
            name="insuranceIssuedLoc"
            value={state.insuranceIssuedLoc}
            onChange={handleInputChange}
          />
          <CustomDatePicker
            disabled={isAddOrEditMode}
            label="Date Issued"
            onChange={(e: any) => {
              customInputchange(e, "insuranceDateIssued");
            }}
            value={new Date(state.insuranceDateIssued)}
          />
        </fieldset>
      </div>
      <fieldset
        style={{
          display: "flex",
          flexDirection: "column",
          rowGap: "10px",
          padding: "15px",
          border: "1px solid #cbd5e1",
          borderRadius: "5px",
        }}
      >
        <legend>Premiums</legend>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            padding: "5px",
          }}
        >
          <Button
            disabled={isAddOrEditMode}
            size="small"
            variant="contained"
            color="primary"
            onClick={() => {
              computation();
            }}
          >
            Compute
          </Button>
        </div>

        <div
          style={{
            display: "grid",
            gridTemplateColumns: "repeat(2,1fr)",
            gap: "10px",
          }}
        >
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Insured Value"
            name="insuredValue"
            value={state.insuredValue}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Percentage"
            name="percentagePremium"
            value={state.percentagePremium}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Total Premium"
            name="totalPremium"
            value={state.totalPremium}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Vat"
            name="vat"
            value={state.vat}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Doc Stamp"
            name="docStamp"
            value={state.docStamp}
            onChange={handleInputChange}
          />
          <div style={{ display: "flex", gap: "5px", position: "relative" }}>
            <TextField
              sx={{ width: "80px" }}
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              name="localGovTaxPercent"
              value={state.localGovTaxPercent}
              onChange={handleInputChange}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Local Gov Tax"
              name="localGovTax"
              value={state.localGovTax}
              onChange={handleInputChange}
            />
          </div>
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Notary/Misc(umis)"
            name="umis"
            value={state.umis}
            onChange={handleInputChange}
          />
           <TextField
            disabled={isAddOrEditMode}  
            required
            variant="outlined"
            size="small"
            label="Notary Misc(Principal)"
            name="principal"
            value={state.principal}
            onChange={handleInputChange}
          />
        </div>
        <Divider color="secondary" />
        <TextField
          disabled={isAddOrEditMode}
          required
          variant="outlined"
          size="small"
          label="Total Due"
          name="totalDue"
          value={state.totalDue}
          onChange={handleInputChange}
        />
      </fieldset>
    </div>
  );
}
